// This is Link API
import Link from "next/link";
import Dashboard from "../components/Dashboard.js";
import React from "react";
import fetch from 'isomorphic-unfetch';
import ProjectsTable from "../components/ProjectsTable.js";
import AddProjectDialog from './../components/AddProjectDialog';

class Index extends React.Component {
  static async getInitialProps() {
    const res = await fetch("http://localhost:7900/api/all-project");
    const data = await res.json();
    console.log(res);

    return {
      projects: data
    };
  }

  state = {
    open: false,
    projects: this.props.projects
  };

  handleClickOpen = () => {
    this.setState({ open: true });
  };

  handleClose = () => {
    this.setState({ open: false });
  };

  handleAddProject = (project_name, project_desc) =>{
    const axios = require("axios");
    axios
      .post("http://localhost:7900/api/create-project", {
        project_name: project_name,
        project_desc: project_desc
      })
      .then(function(response) {
        console.log(response);
      })
      .catch(function(error) {
        console.log(error);
      });
    this.props.handleClose();
  };
  



  render() {
    return (
      <div>
        <Dashboard title="Project Manage">
        <Button
            variant="contained"
            color="primary"
            onClick={this.handleClickOpen}
          >
            Add Project
          </Button>
          <AddProjectDialog
            handleClose={this.handleClose}
            open={this.state.open}
            handleAddProject={this.handleAddProject}
          />
          <ProjectsTable projects={this.state.projects}/>
        </Dashboard>
      </div>
    );
  }
}

export default Index;
